import axios from "axios";

let url = "http://212.107.253.70:8090";

export default {
  state: {
    user: {
      id: 1,
      username: "Вадим",
      phone: "89969389618",
      email: "vadim@vadim.com",
      avatar: null,
      status: "contractor"
    },
    isLoading: false
  },
  actions: {
    async loginUser(ctx, { login, password }) {
      console.log(login, password);
      return await axios.post(url + "/site/login", {
        email_phone: login,
        password: password
      });
    },
    async logoutUser() {
      return await axios.post(url + "/site/logout");
    }
  },
  getters: {
    userName(state) {
      return state.username;
    },
    userAvatar(state) {
      return state.avatar;
    },
    userStatus(state) {
      return state.user.status;
    }
  },
  mutations: {
    setUserLoadStatus(state, status) {
      state.user.status = status;
    },
    setUser(state, user) {
      state.user = user;
    }
  }
};
