import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  { path: "/", redirect: "/authorization" }, //заменить на редирект к авторизации
  {
    path: "/authorization",
    meta: {
      layout: "Shadow"
    },
    component: () => import("@/components/authorization/Authorization")
  },
  {
    path: "/forgotPassword",
    meta: {
      layout: "Shadow"
    },
    component: () => import("@/components/ForgotPassword")
  },
  {
    path: "/requests/add",
    meta: {
      layout: "Main"
    },
    component: () => import("@/components/allRequests/RequestAdd.vue")
  },
  {
    path: "/requests/:requestId",
    meta: {
      layout: "Main"
    },
    component: () =>
      import("@/components/soloRequest/requestCustomer/RequestCustomer.vue")
  },
  {
    path: "/responses/:respondId",
    meta: {
      layout: "Main"
    },
    component: () =>
      import("@/components/soloResponse/responseCustomer/ResponseCustomer.vue")
  },
  {
    path: "/:type",
    name: "home",
    meta: {
      layout: "Main"
    },
    component: () => import("@/views/Content.vue")
  }
  /*
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
