let url = "http://localhost:3000";

export default {
  state: {
    contractors: [
      {
        id: 1,
        name: "Иван",
        city: "Мытищи",
        experience: 12,
        cost: 300,
        kindJob: "Отделка потолков",
        avatar: null
      },
      {
        id: 2,
        name: "Тимур",
        city: "Мытищи",
        experience: 12,
        cost: 300,
        kindJob: "Дибилизм",
        avatar: null
      }
    ]
  },
  actions: {
    async fetchContractorById({ commit }, id) {
      let request = await fetch(url + "/contractors/" + id);
      let contractor = await request.json();
      console.log(contractor, id);

      if (contractor !== undefined) {
        commit("addContractor", contractor);
      }
    }
  },
  getters: {
    getContractorById: state => id => {
      return state.contractors.find(contractor => contractor.id === id);
    }
  },
  mutations: {
    addContractor(state, contractor) {
      if (!state.contractors.includes(contractor)) {
        state.contractors.push(contractor);
      }
    }
  }
};
