let url = "http://212.107.253.70:8090";

export default {
  state: {
    respondsStatus: [{}],
    isStatusLoading: false
  },
  actions: {
    async fetchAllRespondStatus({ commit }) {
      commit("changeLoadStatus", true);

      let statuses = await fetch(url + `/status-response`);
      statuses = await statuses.json();

      commit("setRespondsStatus", statuses[0]);
      commit("changeLoadStatus", false);
    }
  },
  getters: {
    getResponseStatus: state => id => {
      return state.respondsStatus.find(r => r.id === id);
    }
  },
  mutations: {
    setRespondsStatus(state, statuses) {
      state.respondsStatus = statuses;
    },
    changeLoadStatus(state, status) {
      state.isStatusLoading = status;
    }
  }
};
