<template>
  <div>
    <nav class="sidebar">
      <ul>
        <li v-for="menu in menus" :key="menu.name">
          <router-link :class="menuStyle(menu.to)" :to="menu.to">{{
            menu.name
          }}</router-link>
        </li>
      </ul>
      <div class="filler" @click="closeMenu"></div>
    </nav>
  </div>
</template>

<script>
export default {
  name: "Sidebar",
  data: () => ({
    menus: [
      { name: "Заявки", to: "/requests" },
      { name: "Отклики", to: "/responses" },
      { name: "Заказы", to: "/orders" }
    ]
  }),
  methods: {
    menuStyle(to) {
      if (to === "/" + this.$route.params.type) {
        return "sbar-item active";
      } else {
        return "sbar-item";
      }
    },
    closeMenu() {
      this.$store.commit("changeMenuStatus");
    }
  }
};
</script>

