export default {
  state: {
    responses: [
      {
        id: 1,
        contractorId: 1,
        request_id: 1,
        description:
          "Описание строительных работ вот такое вот длинное получилось, что аж не меньше чем в две строки, а лучше больше. И вот теперь, когда мы зашли на страницу заявки, мы можем видеть это описание целиком, ну, не замечательно ли?",
        cost: 1,
        period: 1000,
        status: "Принято",
        creationTime: 21133113,
        updateTime: 211311441
      },
      {
        id: 2,
        contractorId: 1,
        request_id: 4,
        description:
          "Описание строительных работ вот такое вот длинное получилось, что аж не меньше чем в две строки, а лучше больше. И вот теперь, когда мы зашли на страницу заявки, мы можем видеть это описание целиком, ну, не замечательно ли?",
        cost: 1,
        period: 1000,
        status: "Принято",
        creationTime: 21133113,
        updateTime: 211311441
      },
      {
        id: 3,
        contractorId: 2,
        request_id: 4,
        description:
          "Описание строительных работ вот такое вот длинное получилось, что аж не меньше чем в две строки, а лучше больше. И вот теперь, когда мы зашли на страницу заявки, мы можем видеть это описание целиком, ну, не замечательно ли?",
        cost: 1,
        period: 1000,
        status: "Принято",
        creationTime: 21133113,
        updateTime: 211311441
      }
    ],
    isLoading: false
  },
  actions: {
    async fetchAllResponses({ commit }) {
      commit("changeLoadStatus", true);

      let response = await fetch(url + "/response?Response[period]=1000000");
      let output = await response.json();

      commit("changeLoadStatus", false);
      commit("setResponses", output[0]);
    },
    async fetchResponseById({ commit }, id) {
      commit("changeLoadStatus", true);

      let response = await fetch(
        url + `/response?Response[id]=${id}&Response[period]=1000000`
      );
      let output = await response.json();

      commit("changeLoadStatus", false);
      commit("addResponses", output[0]);
    }
  },
  getters: {
    getAllResponses(state) {
      return state.responses;
    },
    getResponseById: state => id => {
      return state.responses.filter(response => response.request_id === +id);
    }
  },
  mutations: {
    setResponses(state, responses) {
      state.responses = responses;
    },
    addResponses(state, responses) {
      responses.forEach(respond => {
        if (state.responses.filter(r => r.id === respond.id).length === 0) {
          state.responses.push(respond);
        }
      });
    },
    changeLoadStatus(state, status) {
      state.isLoading = status;
    }
  }
};
